<template>
	<view style="background-color: white; height: 650rpx;">
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }"  title="关联TimeLaw" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
			<u-top-tips ref="uTips" :navbar-height="statusBarHeight + navbarHeight"></u-top-tips>
		</view>
		
		<view class="item">
			
			<view class="input">
				<view id="username">用户名</view>
				<view class="inputUsername">
					<u-input v-model="Form.username" type="text" border="true" placeholder="请输入用户名" />
				</view>
			</view>
			
			<view class="input">
				<view id="password">密码</view>
				<view class="inputPassword">
					<u-input v-model="Form.password" type="password" border="true" password-icon="true" placeholder="请输入密码" /> 
				</view>
			</view>
			
			<view class="bottom">
				
				<view class="submitButton" >
					<u-button type="primary" @click="edit()">确定</u-button> 
				</view>
				
				<view class="gotoRegist" @click="goToRegistPage()">
					没有账号，<span>立即注册</span>
				</view>
			</view>
			
		</view>
		
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 状态栏高度，H5中，此值为0，因为H5不可操作状态栏
				statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
				// 导航栏内容区域高度，不包括状态栏高度在内
				navbarHeight: 44,
				Form:{
					username: '',
					password: ''
				},
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				}
			}
		},
		methods:{
			goToRegistPage(){
				uni.navigateTo({
					url:'../regist/regist'
				})
			},
			edit(){
				uni.request({
					url:'http://localhost/users/relationTimeLaw',
					method:"POST",
					header:{
						"token": uni.getStorageSync('token')
					},
					data:{
						"username": this.Form.username,
						"password": this.Form.password
					},
					success: (e) => {
						console.log(e);
						uni.setStorageSync('token',e.data.data.token);
						uni.showToast({
							icon:"success",
							title:'关联成功'
						});
						setTimeout(function(){
							uni.navigateBack({
								
							})
						},1500);
					},
					fail(e) {
						this.$refs.uTips.show({
							title: '用户名或密码错误',
							type: 'error'
						});
					}
				});
			}
		}
	}
</script>

<style scoped>
	.item{
		margin-left: 30rpx;
		margin-top: 80rpx;
	}
	.inputPassword{
		width: 550rpx;
		/* border: 1px solid #000; */
	}
	.inputUsername{
		width: 550rpx;
	}
	.input{
		display: flex;
		margin-top: 30rpx;
	}
	#username,#password{
		font-size: 30rpx;
		height: 70rpx;
		line-height: 70rpx;
		margin-right: 30rpx;
	}
	#password{
		margin-left: 30rpx;
	}
	.bottom{
		display: flex;
		margin-top: 50rpx;
	}
	.submitButton{
		margin-top: 50rpx;
		width: 200rpx;
		margin-left: 230rpx;
	}
	.gotoRegist{
		margin-top: 90rpx;
		margin-left: 30rpx;
	}
	.gotoRegist span{
		color: #1E90FF;
		text-decoration: underline;
	}
</style>